import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AtivarComponents } from '../shared/services/ativarComponents';


@Component({
    selector: 'app-layout',
    templateUrl: './layout.component.html',
    styleUrls: ['./layout.component.scss']
})
export class LayoutComponent implements OnInit {

    constructor(private router: Router, public showComponents: AtivarComponents) {
        this.verificLogin();
    }

    verificLogin() {
        const email = localStorage.getItem('email');
        if (!email) {
            this.showComponents.show = false;
            this.router.navigate(['login']);
        } else {
            this.showComponents.show = true;
            this.router.navigate(['bolsonaro']);
        }
    }

    ngOnInit() { }
}
